.photos {
  display: flex;
  justify-content: space-between;

  &__img {
    width: calc(50% - 10px);
    height: 256px;
    object-fit: cover;
    object-position: center;
  }

  &__wide {
    &--right {
      .photos__img:first-child {
        width: calc(33% - 10px);
      }

      .photos__img:last-child {
        width: calc(67% - 10px);
      }
    }

    &--left {
      .photos__img:first-child {
        width: calc(67% - 10px);
      }

      .photos__img:last-child {
        width: calc(33% - 10px);
      }
    }
  }
}
